
/* The whole page shared element */

.app-container {
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgba(152, 215, 228, 0.6);
  color: #F3F4F6;
}

/* All pages shared the same style of nav bar */

nav {
    width: 100%;
    background-color: rgba(253, 250, 180, 0.6);
    display: flex;
    justify-content: space-around;
    align-items: center;
    .nav-links {
        list-style: none;
        display: flex;
        .nav-icon a {
            display: inline-block;
            text-decoration: none;
            color: rgb(0, 0, 0);
            padding: 1em;
            margin-right: 2em;
            &:hover {
                color: rgb(235, 101, 130);
                font-weight: bold;
            }
        }
    }
}

nav.vertical {
    width: 150px;
    height: 100vh;
    flex-direction: row;
    position: absolute;
    left: 0;
    top: 0;
    .nav-links {
        flex-direction: column;
        .nav-item a {
            display: block;
            padding: 4em 0;
            
        }
    }

}

/* title and subtitle element style */

#title {
  font-size: 40px;
  font-weight: bold;
  color:rgb(44, 43, 43);
  margin:auto;
  margin-top: 50px;
	width: 1050px;
	display: flex;
	border-radius: 5px;
}

#subtitle {
  font-size: 20px;
  color:rgb(44, 43, 43);
  margin:auto;
	width: 1050px;
	display: flex;
}

/* Pomodoro timer elements style and color setting , reference: "https://codepen.io/dcode-software/pen/XWgyOpg" */

  .timer {
    display: inline-block;
    padding: 24px 32px;
    margin: 50px auto 40px 740px;
    border-radius: 40px;
    background: #111827;
  }
  
  .timer__part {
    font-size: 70px;
  }
  
  .timer__btn {
    width: 70px;
    height: 70px;
    margin-left: 16px;
    border-radius: 50%;
    border: none;
    color: white;
    background: #08a0e6;
    cursor: pointer;
  }
  
  .timer__btn--start {
    background: #00b84c;
  }
  
  .timer__btn--stop {
    background: #DC143C;
  }
  

  /* Stopwatch timer elements style and color setting, reference: "https://github.com/TylerPottsDev/yt-js-stopwatch" */

  .watch {
    text-align: center;
    background-color: #111827;
    max-width: 450px;
    margin: 4rem auto;
    padding: 1rem 2rem;
    border-radius: 1rem;
  }
  
  .watch .time {
    font-size: 100px;
    font-weight: 300;
    margin-bottom: 1rem;
  }
  
  .controls {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .controls button {
    appearance: none;
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
  
    margin: 0 0.5rem;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
  
    color: #FFF;
    background-color: #DC143C;
  
    transition: 0.4s;
    user-select: none;
  }
  
  .controls button:hover {
    opacity: 0.7;
  }
  
  .controls button:active {
    background-color: #1F2937;
  }